var m$p = {
	metadb: {}
};


var locales = {
	ko_KR: {
		"L_PWD_F_CHAR": "문자로 시작해야 합니다!",
		"L_PWD_F_NUM": "최소 한개 이상의 숫자를 사용해 주십시오!",
		"L_PWD_F_LOW_CHAR": "최소 한개 이상의 소문자를 사용해 주십시오!",
		"L_PWD_F_UPP_CHAR": "최소 한개 이상의 대문자를 사용해 주십시오!",
		"L_PWD_F_SPEC_CHAR": "최소 한개 이상의 @ 특수문자를 사용해 주십시오!",
		"L_PWD_F_WHT_SPACE": "스페이스는 허용되지 않습니다!",
		"L_PWD_F_LEN": "최소 @ 자 이상을 사용해 주십시오!"
	},
	en_US: {
		"L_PWD_F_CHAR": "Start of string!",
		"L_PWD_F_NUM": "At least one digit!",
		"L_PWD_F_LOW_CHAR": "At least one lower case!",
		"L_PWD_F_UPP_CHAR": "At least one upper case!",
		"L_PWD_F_SPEC_CHAR": "One or more @ special character!",
		"L_PWD_F_WHT_SPACE": "No white space allowed!",
		"L_PWD_F_LEN": "At least @ places!"
	}
}

IG$.ShowErrorMessage = function(jdoc, panel) {
	var detail = jdoc.detail;
	
	
	if (jdoc.errorcode == "0x8f00")
	{
		// password error
		window.useLocale = window.useLocale || "en_US";
		
		var errmsg = detail;
		
		if (errmsg)
		{
			var em = errmsg.split("|"),
				emm = ["<br/><p>"];
			
			$.each(em, function(i, t) {
				var st;
				
				if (!t)
					return;
					
				if (t.indexOf("L_PWD_F_SPEC_CHAR_") > -1)
				{
					st = t.substring("L_PWD_F_SPEC_CHAR_".length);
					t = "L_PWD_F_SPEC_CHAR";
				}
				else if (t.indexOf("L_PWD_F_LEN_") > -1)
				{
					st = t.substring("L_PWD_F_LEN_".length);
					t = "L_PWD_F_LEN";
				}
				
				var m = locales[window.useLocale][t];
				
				if (m.indexOf("@") > -1 && st)
				{
					m = m.substring(0, m.indexOf("@")) + st + m.substring(m.indexOf("@") + 1);
				}
				
				emm.push(m + "<br/>");
			});
			
			emm.push("</p>");
			errmsg = emm.join("");
			
			detail = errmsg;
		}
	}
	
	showMessage(jdoc.errormsg + "<br/>" + (detail ? "\n" + detail : "") + "<br/>" + jdoc.errorcode, "Error! [" + jdoc.errorcode + "]", true, null);
}

IG$.ShowConErr = function(panel) {
	showMessage("Error while connecting to server", "Error!", true, null);
}

function callback(thisobj, success, param) {
	this.thisobj = thisobj;
	this.success = success;
	
	this.execute = function(extra) {
		this.success.call(this.thisobj, extra, param);
	};
}

function showMessage(msg, title, modal, rs) {
	var dlg1 = $("#d_error_01"),
		dlg2 = $("#d_error_02");
	
	dlg1.html(msg);
	dlg1.fadeIn();
	
	dlg2.html(msg);
	dlg2.fadeIn();
}

function clearMessage() {
	var dlg1 = $("#d_error_01"),
		dlg2 = $("#d_error_02");
	
	dlg1.fadeOut();
	dlg2.fadeOut();
}

function goMain() {
	var gurl = ig$.mainlink + "/";
	document.location.href = gurl;
}

function doUpdate() {
	var mi = ig$.mi,
		callback = new IG$.callBackObj(mi, mi.doUpdate);
	
	$("#main-area").scrollTop(20000);
	showMessage("Continue to update the server?", "Confirm", true, callback);
}

var ig$ = window.ig$ || {};

function init_events() {
	var i,
		bpath = window.location.pathname.split("/"),
		mpath = [];
	
	for (i=0; i < bpath.length; i++)
	{
		if (bpath[i] == "install")
		{
			break;
		}
		else
		{
			mpath.push(bpath[i]);
		}
	}

	ig$.mainlink = mpath.join("/");
	ig$.servlet = ig$.mainlink + "/servlet/ircp";
	ig$.mi = new $MI();
	
	$.each(["01", "02"], function(i, b) {
		var btn = $("#tab_" + b);
		
		btn.bind("click", function(e) {
			e.stopPropagation();
			e.preventDefault();
			
			$.each(["01", "02"], function(i, t) {
				$("#tab_" + t)[t == b ? "addClass" : "removeClass"]("selected");
				var ta = $("#tab_content_" + t);
				ta[t == b ? "show" : "hide"]();
			});
		});
	});
	
	var db_type = $("#db_type");
	db_type.empty();
	
	$.each(ig$.mi.jdbc_template, function(i, drv) {
		var t = $("<option value='" + i + "'>" + drv.name + "</option>").appendTo(db_type);
	});
	
	$("#btn_start_form").bind("click", function(e) {
		e.stopPropagation();
		e.preventDefault();
		
		ig$.mi.getInstallStat();
	});
	
	$("#btn_back_inst").bind("click", function(e) {
		e.stopPropagation();
		e.preventDefault();
		
		$("#dpage_01").show();
		$("#dpage_02").hide();
		
		$("#main-area").scrollTop(0);
	});
	
	$("#btn_start_install").bind("click", function(e) {
		e.stopPropagation();
		e.preventDefault();
		
		ig$.mi.doUpdate();
	});
	
	$("#btn_check_dburl").bind("click", function(e) {
		e.stopPropagation();
		e.preventDefault();
		
		ig$.mi.updateJdbcUrl();
	});
	
	$("#btn_check_con").bind("click", function(e) {
		e.stopPropagation();
		e.preventDefault();
		
		ig$.mi.checkDatabaseCon();
	});
		
	$("#btn_check_con2").bind("click", function(e) {
		e.stopPropagation();
		e.preventDefault();
		
		var is_jndi = $("#db_isjndi").is(":checked");
		
		ig$.mi.checkDatabaseCon(is_jndi ? ["md_path"] : ["md_user", "md_password"], "db_con_status2");
	});
	
	$("#btn_start").bind("click", function(e) {
		e.stopPropagation();
		e.preventDefault();
		
		goMain();
	});
	
	$("#db_type").bind("change", function() {
		ig$.mi.updateDesc();
	});
	
	$("#db_isjndi").bind("change", function() {
		ig$.mi.updateJNDI();
	});
	
	ig$.mi.setLoading(false);
	
	$.each(["db_host", "db_port", "db_name", "db_user", "db_path", "md_owner", "md_user"], function(i, k) {
		var ctrl = $("#msg_" + k),
			tctrl = $("#" + k);
		
		ctrl.bind("click", function(e) {
			var mtext = ctrl.text();
			
			if (mtext && mtext.substring(0, 3) == "Ex)")
			{
				tctrl.val(mtext.substring(4) || "");
			}
		});
	});
	
	ig$.mi.updateDesc();
}

$(document).ready(function() {
	init_events();
});
