$MI.prototype = {
	setLoading: function(visible, prog) {
		var mloading = $("#loading"),
			progressbar = $("#progressbar", mloading),
			progress_gauge = $("#progress_gauge", progressbar);
			
		if (visible == true)
		{
			progress_gauge.css({width: (prog || 100) + "%"});
			mloading.show();
		}
		else
		{
			progress_gauge.css({width: ("" + 100) + "%"});
			setTimeout(function() {
				mloading.fadeOut();
			}, 200);
		}
	},
	
	updateJNDI: function() {
		var me = this,
			is_jndi = $("#db_isjndi").is(":checked");
			
		if (is_jndi)
		{
			$("#form_db_jndi").show();
			$("#form_db_jdbc").hide();
			
			$("#dd_jdbc").hide();
			$("#dd_jndi").show();
			
			$("#msg_db_path").html("Ex) jdbc/amplix_meta");
		}
		else
		{
			$("#form_db_jndi").hide();
			$("#form_db_jdbc").show();
			
			$("#dd_jdbc").show();
			$("#dd_jndi").hide();
		}
	},
	
	updateDesc: function() {
		var me = this,
			dbtype = $("#db_type").val(),
			tmpl,
			i;
		
		if (dbtype && me.jdbc_template[dbtype])
		{
			tmpl = me.jdbc_template[dbtype];
			
			$.each(["msg_db_host", "msg_db_port", "msg_db_name", "msg_db_user", "msg_md_owner", "msg_md_user"], function(i, k) {
				var ctrl = $("#" + k);
				
				ctrl.html("Ex) " + (tmpl[k] || ""));
			});
		}
	},
	
	updateJdbcUrl: function() {
		var me = this,
			dbtype = $("#db_type").val(),
			jdbc = "",
			db_url = this.jdbc_template[dbtype],
			t, p = ["db_host", "db_port", "db_name"],
			cdef = me.cdef,
			app_path,
			arr;
		
		if (db_url)
		{
			$("#db_jdbc_driver").val(db_url.driver);
			$("#db_validatesql").val(db_url.validatesql);
			t = db_url.jdbc;
			
			$.each(p, function(i, m) {
				var ctrl = $("#" + m);
				me.clearError(ctrl);
				if (t.indexOf(m) > -1)
				{
					if (!ctrl.val())
					{
						me.setError(ctrl);
					}
					else
					{
						t = t.replace(m, ctrl.val());
					}
				}
			});
			
			if (cdef && cdef.app_path && t.indexOf("app_home") > -1)
			{
				app_path = cdef.app_path;
				
				arr = app_path.split("\\\\");
				
				app_path = arr.join("/");
				
				if (app_path.charAt(app_path.length - 1) == '/')
				{
					app_path = app_path.substring(0, app_path.length - 1);
				}
				
				t = t.replace("app_home", cdef.app_path);
			}
			
			jdbc = t;
		}
		
		$("#db_jdbc_url").val(jdbc);
	},
		
// get installation information
	getInstallStat: function() {
		var me = this, 
			req = new IG$._rpc$();
		
		me.setLoading(true);
			
		req.init(me, 
			{
				ack: "sysinfo",
				payload: {},
				mbody: {},
				_lang_: window.useLocale
			}, me, me.rs_getInstallStat, function(resp) {
				var errcode = resp.errorcode,
					clsname = "No";
				
				IG$.ShowErrorMessage(resp);
				
				$("#app_home").html("<span style='color:#ff0000'>Not defined</span>");
			});
		req.send();
	},
	
	rs_getInstallStat: function(resp) {
		var i,
			clsname = null,
			cdef = resp,
			sel_modules = $("#sel_modules");
		
		this.cdef = resp;
		
		var v1 = cdef.info.version.split("."),
			v2 = cdef.serverversion ? cdef.serverversion.split(".") : [],
			meta_db;
		
		if (cdef.needlogin || cdef.serverversion)
		{
			$(".error-desc", $("#d_error_01")).html("For security reasons this script will only work with a new and unmodified AmplixBI installation. You should either re-extract the files from the download package or consult the complete instruction.");
			$("#btn_start_01").remove();
			$("#d_error_01").show();
			return;
		}
		
		if (!cdef.app_path)
		{
			$(".error-desc", $("#d_error_01")).html("APP_HOME location does not specified or configured with wrong place. Please check instruction and try again!");
			$("#d_error_01").show();
			return;
		}
		
		sel_modules.empty();
		
		if (cdef.modules)
		{
			$.each(cdef.modules, function(i, m) {
				var mid = "module_" + i,
					t = [
						'<tr>',
						'<td><input type="checkbox" id="' + mid + '"></input></td>',
						'<td><label for="' + mid + '"><span class="module_name">' + m.name + '</span><br/><span class="module_desc">' + m.desc + '</span></label></td>',
						'</tr>'
					], d, c;
				
				d = $(t.join("")).appendTo(sel_modules);
				c = $("#" + mid, d);
				c.bind("change", function(e) {
					m.selected = c.prop("checked");
				});
			});
		}
		
		$("#dpage_01").hide();
		$("#dpage_02").show();
		$("#dpage_03").hide();
		
		$("#main-area").scrollTop(0);
				
		if (cdef.meta_databases)
		{
			for (i=cdef.meta_databases.length-1; i>=0; i--)
			{
				if (cdef.meta_databases[i].name == "IGCBASE")
				{
					meta_db = cdef.meta_databases[i];
					break;
				}
			}
			
			if (meta_db)
			{
				cdef.dbtype = meta_db.dataBaseType;
			}
		}
		
		$("#app_home").html("<span style='color:#0000ff'>" + (cdef.app_path) + "</span>");
		
		$("#s_version").html(cdef.info.version);
		
		if (cdef.dbconnection)
		{
			$("#db_type").val(cdef.dbtype);
		}
	},
	
	updateCnt: function(fields) {
		var me = this,
			cdef = me.cdef,
			is_jndi = $("#db_isjndi").is(":checked"),
			meta_db = cdef.meta_databases[0],
			r = true;
			
		meta_db.is_jndi = is_jndi;
		
		$.each(fields, function(i, f) {
			var fdiv = $("#" + f);
			
			me.clearError(fdiv);
			
			if (!fdiv.val())
			{
				me.setError(fdiv);
				r = false;
				
				return false;
			}
			
			switch (f)
			{
			case "s2adminname":
				cdef.adminname = fdiv.val();
				break;
			case "s2adminpwd":
				cdef.adminpwd = fdiv.val();
				break;
			case "s2adminpwd_confirm":
				if (cdef.adminpwd != fdiv.val())
				{
					me.setError(fdiv);
					r = false;
				}
				break;
			case "db_type":
				meta_db.dataBaseType = fdiv.val();
				break;
			case "db_jdbc_driver":
				meta_db.jdbcDriver = fdiv.val();
				break;
			case "db_jdbc_url":
				meta_db.jdbcUrl = fdiv.val();
				break;
			case "db_name":
				meta_db.schemaname = fdiv.val();
				break;
			case "db_user":
			case "md_user":
				meta_db.userID = fdiv.val();
				break;
			case "db_passwd":
			case "md_password":
				meta_db.password = fdiv.val();
				break;
			case "db_validatesql":
				meta_db.validateSQL = fdiv.val(); 
				break;
			case "db_path":
				meta_db.db_path = fdiv.val();
				break;
			}
		});
		
		return r;
	},
	
	setError: function(field) {
		$($(field.parent()).parent()).addClass("form-error");
	},
	
	clearError: function(field) {
		$($(field.parent()).parent()).removeClass("form-error");
	},
	
	checkDatabaseCon: function(user_fields, ofd) {
		var me = this,
			cdef = me.cdef, 
			req = new IG$._rpc$(),
			is_jndi = $("#db_isjndi").is(":checked"),
			field = ["db_type", "db_host", "db_port", "db_jdbc_driver", "db_jdbc_url", "db_validatesql"],
			ofield = "db_con_status",
			dbconf;
			
		ofield = ofd || ofield;
		
		if (is_jndi)
		{
			field = ["db_type", "db_path"];
		}
		else
		{
			if (user_fields)
			{
				field = field.concat(user_fields);
			}
			else
			{
				field = field.concat(["db_user", "db_passwd"]);
			}
		}
		
		clearMessage();
		
		cdef.meta_databases = [{}];
		
		if (me.updateCnt(field) == false)
			return;
		
		if (user_fields)
		{
			dbconf = cdef.meta_databases[0];
			switch (dbconf.dataBaseType)
			{
			case "mysql":
			case "mariadb":
			case "postg":
			case "mssql":
				if (dbconf.jdbcUrl.indexOf($("#db_name").val()) > -1)
				{
					dbconf.jdbcUrl = dbconf.jdbcUrl.replace($("#db_name").val(), $("#md_owner").val());
				}
				break;
			}
		}
		
		me.setLoading(true);
		
		$("#" + ofield).empty();
		$("#" + ofield).hide();
		
		req.init(me, 
			{
				ack: "checkdbinfo",
				meta_type: "config_info",
				payload: {},
				mbody: cdef,
				_lang_: window.useLocale
			}, me, function(resp) {
				$("#" + ofield).html("Success on database connection!");
				$("#" + ofield).fadeIn();
			}, function(resp) {
				this.setLoading(false);
				IG$.ShowErrorMessage(resp);
				$("#" + ofield).html("<span style='color:#ff0000'>Error on database connection!</span>");
				$("#" + ofield).fadeIn();
			});
		req.send();
	},
	
	doUpdate: function() {
		var me = this,
			cdef = me.cdef, 
			i,
			req = new IG$._rpc$(),
			is_jndi = $("#db_isjndi").is(":checked"),
			dbfield = is_jndi ? ["db_type", "db_path"] : ["db_type", "db_host", "db_name", "db_port", "db_user", "db_passwd"],
			field = is_jndi ? ["s2adminname", "s2adminpwd", "s2adminpwd_confirm"] : ["db_jdbc_driver", "db_jdbc_url", "db_validatesql", "s2adminname", "s2adminpwd", "s2adminpwd_confirm"],
			md_user = $("#md_user"),
			md_password = $("#md_password")
			md_owner = $("#md_owner"),
			md_path = $("#md_path");
		
		clearMessage();
		
		cdef.meta_databases = [{}];
		
		if (me.updateCnt(dbfield) == false)
			return;
		
		if (me.updateCnt(field) == false)
			return;
		
		me.clearError(md_owner);
		me.clearError(md_user);
		me.clearError(md_password);
		me.clearError(md_path);
		
		for (i=0; i < cdef.modules.length; i++)
		{
			if (cdef.modules[i].selected)
			{
				if (is_jndi)
				{
					if (!md_path.val())
					{
						me.setError(md_path);
						return;
					}
				}
				else
				{
					if (!md_owner.val())
					{
						me.setError(md_owner);
						return;
					}
					
					if (!md_user.val())
					{
						me.setError(md_user);
						return;
					}
					
					if (!md_password.val())
					{
						me.setError(md_password);
						return;
					}
				}
				
				cdef.modules[i].schemaname = md_owner.val();
				cdef.modules[i].db_user = md_user.val();
				cdef.modules[i].db_passwd = md_password.val();
				cdef.modules[i].db_path = md_path.val();
				cdef.modules[i].db_is_jndi = is_jndi;
			}
		}
		
		me.setLoading(true);
		
		req.init(me, 
			{
				ack: "updateconfig",
				meta_type: "config_info",
				payload: {},
				mbody: cdef,
				_lang_: window.useLocale
			}, me, me.rs_doUpdate, function(resp) {
				this.setLoading(false);
				IG$.ShowErrorMessage(resp);
			});
		req.stoploading = false;
		req.send();
	},
	
	rs_doUpdate: function(resp) {
		var me = this,
			step = resp.step
		
		me.totalsteps = step;
		me.curstep = 0;
		
		me.processUpdate();
	},
		
	processUpdate: function() {
		var me = this,
			cdef = me.cdef;
		
		me.setLoading(true, 0);
		
		$.ajax({
			url: ig$.servlet,
			type: 'POST',
			contentType: "application/json; charset=UTF-8",
			data: JSON.stringify({
				ack: "security",
				payload: {},
				mbody: {},
				uniquekey: IG$.getUniqueKey()
			}),
			async: true,
			dataType: 'json',
			timeout: 0,
			error: function() {
				alert("Error while connecting server");
			},
			success: function(jdoc, status, xhr) {
				var item = jdoc.result,
					errcode = jdoc.errorcode,
					ltoken;
				
				if (item)
				{
					p1 = item.p1;
					p2 = item.p2;
					
					if (p1 && p2)
					{
						IG$.rsaPublicKeyModules = p1;
						IG$.rsaPublicKeyExpoenent = p2;
						IG$.rsa_kinfo = item.p3;
						
						setTimeout(function() {
							var encpwd = IG$.encryptKey([cdef.adminpwd]);
							me.encpwd = encpwd;
							me.admuser = cdef.adminname;
							me.processUpdate_secure.call(me, encpwd, me.admuser);
						}, 10);
					}
					else
					{
						me.setLoading(false);
						alert("Error while get secure key value. Please try again later!");
					}
				}
				else if (errcode)
				{
					me.setLoading(false);
					IG$.ShowErrorMessage(jdoc);
				}
			}
		});
	},
	
	processUpdate_secure: function(encpwd, admuser) {
		var i,
			me = this, 
			cdef = this.cdef,
			req = new IG$._rpc$(),
			pcnt = (me.totalsteps > 0) ? (me.curstep / me.totalsteps) * 100 : 0;
		
		me.setLoading(true, pcnt);
		
		$.ajax({
			url: ig$.servlet,
			type: 'POST',
			contentType: "application/json; charset=UTF-8",
			data: JSON.stringify({
				ack: "procupdate",
				payload: {
					step: "" + (me.curstep++),
					apu: encpwd[0],
					anm: admuser
				},
				mbody: cdef,
				meta_type: "config_info",
				_lang_: window.useLocale,
				uniquekey: IG$.getUniqueKey()
			}),
			dataType: 'json',
			timeout: 0,
			error: function() {
				alert("Error while connecting server");
			},
			success: function(jdoc, status, xhr) {
				var item = jdoc.result,
					errcode = jdoc.errorcode,
					ltoken;
				
				if (item)
				{
					me.rs_processUpdate.call(me, item);
				}
				else if (errcode)
				{
					me.setLoading(false);
					IG$.ShowErrorMessage(jdoc);
				}
			}
		});
	},
	
	rs_processUpdate: function(jdoc) {
		var me = this;
		
		if (me.curstep < me.totalsteps)
		{
			me.processUpdate_secure(me.encpwd, me.admuser);
		}
		else
		{
			me.rs_updateComplete();
		}
	},
	
	rs_updateComplete: function(jdoc) {
		this.setLoading(false);
		
		// completed
		$("#dpage_01").hide();
		$("#dpage_02").hide();
		$("#dpage_03").show();
		
		$("#main-area").scrollTop(0);
	}
}